/* ============================================
   CENTRALIZED FONT SYSTEM - Currently using Roboto Light
   Change these variables to swap fonts across the entire website

   TYPOGRAPHY SCALE IMPLEMENTED:
   - Header (Hero): 48px, Bold, Uppercase - "Hi, I'm Shareef"
   - Landing Description: 18px, Light - Tagline below name
   - Filter Buttons: 48px, Bold - Same style as header, no caps
   - Project Cards:
     * Name: 16px, Bold - Project titles
     * Description: 14px, Light - Project descriptions
   - Project Pages:
     * Project Name: 36px, Medium - Main project titles
     * Section Names: 28px, Medium - Section headings
     * Body Text: 16px, Light - Paragraphs and bullets
     * Citations: 12px, Light, Italic - Image credits and metadata

   ============================================ */

:root {
  /* FONT FAMILIES - Change these to switch font sets */
  --font-primary: 'RobotoLight', 'Roboto', -apple-system, system-ui, sans-serif; 
  /* --font-mono: 'SF Mono', Monaco, 'JetBrains Mono', monospace; */
  
  /* ========== QUICK FONT SWAPS ==========
     Uncomment one of these to switch your entire website font: */
     
     /* For Futura Geometric: */
     /* --font-primary: 'Futura', 'Futura PT', Avenir, sans-serif; */
     
     /* For Roboto Humanist (Regular):
     --font-primary: 'Roboto', Arial, sans-serif;

     For Roboto Light (Current):
     --font-primary: 'RobotoLight', 'Roboto', -apple-system, system-ui, sans-serif;
     
     For Inter Minimal:
     --font-primary: 'Inter', -apple-system, sans-serif;
     
     For Helvetica Corporate:
     --font-primary: 'Helvetica Neue', Arial, sans-serif;
     
     For Mixed Fonts (Futura + Roboto):
     --font-primary: 'Futura', 'Futura PT', Avenir, sans-serif;
     --font-secondary: 'Roboto', Arial, sans-serif; */
     /* ================================== */ 
  
  /* FONT WEIGHTS - Adjust these for your chosen font
  --weight-thin: 100;
  --weight-light: 300;
  --weight-regular: 400;
  --weight-medium: 500;
  --weight-bold: 700;
  --weight-black: 900;
  
  /* ========== WEIGHT STRATEGY PRESETS ==========
     Uncomment one of these weight sets to match your chosen style:
     
     For Minimal & Light (like Set 1):
     --weight-hero: var(--weight-thin);     // 100
     --weight-heading: var(--weight-light); // 300
     --weight-body: var(--weight-light);    // 300
     
     For Bold Statement (like Set 9):
     --weight-hero: var(--weight-black);    // 900
     --weight-heading: var(--weight-medium); // 500
     --weight-body: var(--weight-regular);  // 400
     
     For Corporate Balance (like Set 2):
     --weight-hero: var(--weight-bold);     // 700
     --weight-heading: var(--weight-medium); // 500
     --weight-body: var(--weight-regular);  // 400
     =========================================== */
  
  /* CURRENT ACTIVE WEIGHTS - Optimized for Roboto Light */
  --weight-hero: var(--weight-bold);     /* H1 weight - bold for impact */
  --weight-heading: var(--weight-medium); /* H2-H6 weight - medium for hierarchy */
  --weight-body: var(--weight-light);    /* Body text weight - light for readability */
  
  /* FONT SIZES - Based on your design system */
  --size-hero: 36px;      /* H1 - Project titles */
  --size-project-name: 36px; /* Project page titles */
  --size-section-name: 24px; /* Section headings */
  --size-large: 36px;     /* H2 - Section headings */
  --size-medium: 28px;    /* H3 - Subsections */
  --size-small: 22px;     /* H4 - Small headings */
  --size-body-large: 18px; /* Landing description */
  --size-body: 16px;      /* Default body text & bullets */
  --size-caption: 14px;   /* Project card descriptions */
  --size-meta: 12px;      /* Very small text & citations */
  --size-project-card-name: 16px; /* Project card names (2px larger than description) */
  
  /* LINE HEIGHTS */
  --lh-tight: 1;   /* For large headings */
  --lh-normal: 1.5;   /* For medium headings */
  --lh-relaxed: 1.5;  /* For body text */
  --lh-loose: 1.5;   /* For long-form content */
  
  /* COLORS */
  --color-text-primary: #0B0F19;
  --color-text-secondary: #6B7280;
  --color-text-muted: #9CA3AF;
  /* Central control for About page links */
  --color-about-link: #9CA3AF;
  
  /* ANIMATED SQUARES COLOR PALETTE */
  --color-square-1: #0B0F19;   /* Primary text color (dark gray-blue) */
  --color-square-2: #6B7280;   /* Secondary text color (medium gray) */
  --color-square-3: #9CA3AF;   /* Muted text color (light gray) */
  --color-square-4: #334155;   /* Dark slate */
  --color-square-5: #475569;   /* Slate 600 */
  --color-square-6: #64748B;   /* Slate 500 */
  --color-square-7: #94A3B8;   /* Slate 400 */
  --color-square-8: #CBD5E1;   /* Slate 300 */
}

/* General Styles */
body {
  font-family: var(--font-primary);
  font-size: var(--size-body);
  font-weight: var(--weight-regular);
  line-height: var(--lh-relaxed);
  color: var(--color-text-primary);
  -ms-overflow-style: none;  /* Hide scrollbar for IE/Edge */
  overflow-y: auto;          /* Keep vertical scrolling */
  overflow-x: hidden;        /* Hide horizontal scrolling */
}

/* ============================================
   CUSTOM SCROLLBAR STYLES
   Hide default browser scrollbars and use CustomScrollbar component instead
   ============================================ */

/* Hide scrollbar for webkit browsers (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: transparent;
}

/* Hide scrollbar for Firefox */
html {
  scrollbar-width: none;
  scroll-behavior: smooth;
}

/* ============================================
   END CUSTOM SCROLLBAR STYLES
   ============================================ */

/* ============================================
   TYPOGRAPHY SYSTEM - Uses centralized variables
   ============================================ */

/* HEADINGS */
h1 {
  font-family: var(--font-primary);
  font-size: var(--size-hero);
  font-weight: var(--weight-bold);
  line-height: var(--lh-tight);
  color: var(--color-text-primary);
  margin-top: 0;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

h2 {
  font-family: var(--font-primary);
  font-size: var(--size-project-name);
  font-weight: var(--weight-medium);
  line-height: var(--lh-normal);
  color: var(--color-text-primary);
  margin-top: 0;
}

h3 {
  font-family: var(--font-primary);
  font-size: var(--size-section-name);
  font-weight: var(--weight-medium);
  line-height: var(--lh-normal);
  color: var(--color-text-primary);
  margin-top: 0;
}

h4 {
  font-family: var(--font-primary);
  font-size: var(--size-small);
  font-weight: var(--weight-heading);
  line-height: var(--lh-normal);
  color: var(--color-text-primary);
  margin-top: 0;
}

h5 {
  font-family: var(--font-primary);
  font-size: var(--size-tiny);
  font-weight: var(--weight-heading);
  line-height: var(--lh-normal);
  color: var(--color-text-primary);
  margin-top: 0;
}

h6 {
  font-family: var(--font-primary);
  font-size: var(--size-micro);
  font-weight: var(--weight-heading);
  line-height: var(--lh-normal);
  color: var(--color-text-primary);
  margin-top: 0;
}

/* BODY TEXT */
p {
  font-family: var(--font-primary);
  font-size: var(--size-body);
  font-weight: var(--weight-body);
  line-height: var(--lh-loose);
  color: var(--color-text-primary);
}

