/* ================================
   ABOUT PAGE - PARAGRAPH-FIRST STYLES
   ================================ */

.about-paragraph p,
.about-paragraph h1,
.about-paragraph h2,
.about-paragraph h3,
.about-paragraph h4,
.about-paragraph h5,
.about-paragraph h6,
.about-paragraph li,
.about-paragraph span,
.about-paragraph div {
  font-family: var(--font-primary);
  font-size: var(--size-body);
  font-weight: var(--weight-body);
  line-height: var(--lh-relaxed);
  color: var(--color-text-primary);
}

.about-paragraph a {
