/* ImageSlider Component Styles */
.image-slider-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-slider-display {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.image-slider-frame {
  width: 100%;
  height: auto;
  display: block;
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.image-slider-controls {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.image-slider-track {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  background: transparent;
  border-radius: 2px;
}

/* Slider Track Background */
.slider-track-bg {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 100%;
  background-color: #d0d0d0;
  border-radius: 2px;
  transform: translateY(-50%);
  transition: background-color 0.2s ease;
}

/* Dark theme support for track background */
[data-theme="dark"] .slider-track-bg,
.dark .slider-track-bg,
body.dark .slider-track-bg {
  background-color: #4a4a4a;
}

/* Progress Track (completed portion) */
.slider-track-progress {
  position: absolute;
  top: 50%;
  left: 0;
  height: 100%;
  background-color: #6b6b6b;
  border-radius: 2px;
  transform: translateY(-50%);
  transition: width 0.1s ease, background-color 0.2s ease;
  z-index: 1;
}

/* Dark theme support for progress track */
[data-theme="dark"] .slider-track-progress,
.dark .slider-track-progress,
body.dark .slider-track-progress {
  background-color: #8a8a8a;
}

/* Slider Thumb */
.slider-thumb {
  position: absolute;
  background-color: #000000;
  border-radius: 0;
  cursor: none;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
  z-index: 2;
  border: none;
  box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);
}

/* Dark theme support for slider thumb */
[data-theme="dark"] .slider-thumb,
.dark .slider-thumb,
body.dark .slider-thumb {
  background-color: #ffffff;
  box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.1);
}

/* Hover effects */
.image-slider-track:hover .slider-thumb {
  box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
  transform: translateX(-50%) translateY(-50%) scale(1.1);
}

[data-theme="dark"] .image-slider-track:hover .slider-thumb,
.dark .image-slider-track:hover .slider-thumb,
body.dark .image-slider-track:hover .slider-thumb {
  box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.3);
}

/* Active/dragging state */
.slider-thumb:active,
.image-slider-track:active .slider-thumb {
  cursor: none;
  transform: translateX(-50%) translateY(-50%) scale(1.2);
}

/* Frame Counter */
.image-slider-counter {
  font-size: 12px;
  color: #666666;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 400;
  letter-spacing: 0.5px;
  user-select: none;
  transition: color 0.2s ease;
}

/* Dark theme support for frame counter */
[data-theme="dark"] .image-slider-counter,
.dark .image-slider-counter,
body.dark .image-slider-counter {
  color: #cccccc;
}

/* Placeholder for when no images are provided */
.image-slider-placeholder {
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  color: #999999;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  border-radius: 4px;
  border: 2px dashed #dddddd;
}

/* Dark theme support for placeholder */
[data-theme="dark"] .image-slider-placeholder,
.dark .image-slider-placeholder,
body.dark .image-slider-placeholder {
  background-color: #2a2a2a;
  color: #666666;
  border-color: #444444;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .image-slider-counter {
    font-size: 11px;
  }
  
  .slider-thumb {
    transform: translateX(-50%) translateY(-50%) scale(1.1);
  }
  
  .image-slider-track:hover .slider-thumb,
  .slider-thumb:active {
    transform: translateX(-50%) translateY(-50%) scale(1.3);
  }
}

/* Accessibility improvements */
.image-slider-track:focus {
  outline: 2px solid #0066cc;
  outline-offset: 2px;
}

[data-theme="dark"] .image-slider-track:focus,
.dark .image-slider-track:focus,
body.dark .image-slider-track:focus {
  outline-color: #4da6ff;
}

/* Animation performance optimization */
.image-slider-frame,
.slider-track-progress,
.slider-thumb {
  will-change: opacity, width, transform;
}

/* Smooth transitions for all interactive elements */
* {
  box-sizing: border-box;
}

.image-slider-container * {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
